export default {
	data() {
		return {
			sign: 0,
			signText: '签到',
			outmoded: -1,
			applyText: '立即报名',
			ImgUrl: this.ImgUrl,
			file_url: '',
			id: 0,
			activity: [],
			detail_url: ''
		}
	},
	methods: {
		// 签到
		toSign() {
			let baseUrl = this.WebUrl
			let that = this
			if(this.outmoded === 0) {
				uni.showModal({
					title: '请先报名活动',
					showCancel: false
				})
			}else if (this.outmoded === 2) {
				uni.showModal({
					title: '活动已结束',
					showCancel:false
				})
			}else {
				if (this.sign === 1) {
					uni.showModal({
						title: '已签到',
						showCancel: false
					})
				}else {
					uni.getStorage({
						key: 'u_id',
						success(res) {
							uni.request({
								url: baseUrl + '/sign',
								method: 'POST',
								data: {
									u_id: res.data,
									activity_id: that.id
								},
								success(resd) {
									if(resd.data.msg === '请求成功!') {
										uni.showModal({
											title: '签到成功',
											showCancel: false,
											success(resde) {
												if(resde.confirm) {
													that.getActDet()
												}
											}
										})
									}
								}
							})
						}
					})
				}
			}
		},
		
		// 报名
		toApply() {
			console.log(this.outmoded)
			if(this.outmoded === 0) {
				uni.navigateTo({
					url: './sign?activity_id=' + this.id
				});
			}else if(this.outmoded === 1){
				uni.showModal({
					title: '已报名',
					showCancel: false
				})
			}else {
				uni.showModal({
					title: '活动已结束',
					showCancel: false
				})
			}
		},
		
		// 获取活动
		getActDet() {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + "/getActDet",
				data: {
					activity_id: that.id
				},
				success: (res) => {
					that.activity = res.data.data
					that.file_url = that.ImgUrl + res.data.data.file_url
					that.detail_url = that.ImgUrl + res.data.data.detail_url
					console.log(that.detail_url)
					that.getUserStatus()
				}
			})
		},
		
		// 获取用户状态
		getUserStatus() {
			let baseUrl = this.WebUrl
			let that = this
			
			uni.getStorage({
				key: 'u_id',
				success(res) {
					uni.request({
						url: baseUrl + '/getUserStatus',
						data: {
							activity_id: that.id,
							u_id: res.data
						},
						success(resd) {
							if (resd.data.data.apply === 1) {
								that.applyText = '已报名'
								that.outmoded = 1
								console.log(that.outmoded)
								
								if (resd.data.data.sign === 1) {
									that.signText = '已签到'
									that.sign = 1
									
								}else {
									that.setUserSign()
								}
							}else {
								that.setUserStatus()
							}
						}
					})
				}
			})
		},
		
		// 修改签到状态
		setUserSign() {
			let timestamp = (new Date()).valueOf();
			
			let beginTime = (new Date(this.activity.begin_time.replace(/-/g,'/'))).getTime()
			
			let endTime = (new Date(this.activity.end_time.replace(/-/g,'/'))).getTime()
			
			if (beginTime <= timestamp <= endTime) {
				this.sign = 0
				this.signText = '立即签到'
			}else {
				this.sign = 1
				this.signText = '已签到'
			}
		},
		
		// 修改报名状态
		setUserStatus() {
			let timestamp = (new Date()).valueOf();
			
			let endTime = (new Date(this.activity.end_time.replace(/-/g,'/'))).getTime()
			
			if (timestamp <= endTime) {
				this.outmoded = 0
				this.applyText = '立即报名'
			}else {
				this.outmoded = 2
				this.applyText = '已结束'
			}
		}
	},
	onLoad:function(option){
		this.id = option.id
	},
	onShow() {
		this.getActDet()
	}
}
